// HEADER

.header {
  display: flex;
  align-items: center;
  padding: 16px 8px;

  color: white;

  .title {
    margin-bottom: 0;
    padding: 0 8px;
  }

  .link {
    padding: 0 8px;

    color: white;
    text-decoration: none;
    font-size: 1.6rem;
  }
}

.container {
  padding-top: 8px;
}

.logo {
  height: 48px;
  margin: 0 8px;
}

// HOME

.vision {
  position: absolute;
  top: 8px;
  right: 16px;

  a {
    color: black;
    text-decoration: none;
  }
}

// INCIDENT

.incident-col {
  padding: 0;
}

.incident-item {

  .incident-img-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 75%;

      padding: 8px;

      border-radius: 50%;
      background-color: lightgray;
    }
  }

  .incident-desc {
    color: black;
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    word-break: break-word;

    margin: 6px;
  }
}

// QUESTION

.arrows {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 8px 8px 8px;

  p {
    font-weight: bold;
  }
}

.custom-radio, .custom-checkbox {
  margin: 5px 0;
}
